.Link {
    border: solid 2px rgb(5, 5, 5);
    background-color: #3f51b5;
    color: #fff;
    border-radius: 4px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
    font-size: 20px;
    width: 48px;
    height: 48px;
    text-decoration: none;
    list-style: none;
    cursor: pointer;
    outline: none;
    margin-left: 10px;
}

.LinkStyled {
    text-decoration: none;
    list-style: none;
    padding-bottom: 15px;
}

.LinkStyledList {
    padding-top: 5px;
}

.AdditionalLinkStyled {
    margin-top: 15px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border: solid 2px rgb(5, 5, 5);
}